import BaseApiService from "../../BaseApiService";

const DEFAULT_PATH = "/custom/api/income/scheme/product";
const DEFAULT_PATH1 = "/custom/api/income/scheme/detail";

class IncomeSchemeService extends BaseApiService {
  getAll = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/search?${search}&start=${start}&limit=${limit}`;
    return this.makeGetRequestWithAuth(url);
  };

  getAllForTable = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH1}/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
  getAllAttachmentDataForIncomeScheme = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/attachment/mapping/search?${search}start=${start}&limit=${limit}`;
    return this.makeGetRequestWithAuth(url);
  };
  getDataForTableByUid = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH1}/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
  getProductInfo = (uid: string) => {
    const url = `${DEFAULT_PATH}/manage/uid/${uid}`;
    return this.makeGetRequestWithAuth(url);
  };
  save = (obj: any) => {
    const url = `${DEFAULT_PATH}/manage`;
    return this.makePostRequestWithAuth(url, obj);
  };

  getPrimaryData = (uid: any = "") => {
    const url = `${DEFAULT_PATH}/manage/uid/${uid}`;
    return this.makeGetRequestWithAuth(url);
  };

  update = (uid: string | undefined, obj: any) => {
    const url = `${DEFAULT_PATH}/manage/${uid}`;
    return this.makePutRequestWithAuth(url, obj);
  };

  findById = (id: string) => {
    const url = `${DEFAULT_PATH}/uid/${id}`;
    return this.makeGetRequestWithAuth(url);
  };
  activate = (uid: string) => {
    const url = `${DEFAULT_PATH}/activate/${uid}`;
    return this.makePutRequestWithAuth(url);
  };
  Inactivate = (uid: string) => {
    const url = `${DEFAULT_PATH}/inactivate/${uid}`;
    return this.makePutRequestWithAuth(url);
  };
  
  modelToAPIParam = () => {
    console.log("object");
  };
}

export default new IncomeSchemeService();
